function isLoggedIn() {
  return g_authInfo != null;
}

function login() {
  signInWithRedirect(auth, provider);
}

function logout() {
  saveAuthInfo(null);
  signOut(auth);
}

function saveAuthInfo(authInfo) {
  if (authInfo) {
    const strAuthInfo = JSON.stringify(authInfo);
    localStorage.setItem("authInfo", strAuthInfo);
  } else {
    localStorage.removeItem("authInfo");
  }

  g_authInfo = authInfo;
  notifyLoggedIn(g_authInfo);
}

