function addLoginCallback(cb) {
  callbacks.push(cb);
}

function notifyLoggedIn(authInfo) {
  for (let cb of callbacks) {
    cb(authInfo);
  }
}

function setUser(userId) {
  setUserId(analytics, userId);
}

export { addLoginCallback, login, logout, isLoggedIn, analytics, setUser, logEvent };
