<template>
  <v-container>
    <v-row justify="center">
      <v-col cols="12" md="8">
        <v-btn text to="/blog" class="mb-4">
          <v-icon left>mdi-arrow-left</v-icon> Back
        </v-btn>
        
        <div v-if="post">
          <div class="d-flex flex-column flex-md-row justify-space-between align-start align-md-end mb-4">
            <div>
              <h1 class="text-h2 mb-2">{{ post.title }}</h1>
              <p class="text-subtitle-1 grey--text mb-0">{{ post.date }}</p>
            </div>
            <ShareButtons
              v-if="post"
              :url="`https://origami-skin.com/blog/${post.slug}`"
              :title="post.title"
            />
          </div>
          
          <v-divider class="my-4"></v-divider>
          
          <!-- Render Markdown Content -->
          <div 
            class="markdown-body" 
            v-html="processedHtml"
          ></div>

          <v-divider class="my-4"></v-divider>

          <div class="d-flex justify-end mt-4 mb-4">
            <ShareButtons
               v-if="post"
               :url="`https://origami-skin.com/blog/${post.slug}`"
               :title="post.title"
            />
          </div>

          <DisqusThread
            v-if="post"
            :identifier="post.slug"
            :url="`https://origami-skin.com/blog/${post.slug}`"
          />
        </div>
        <div v-else>
          <v-alert type="error">Post not found</v-alert>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { getBlogPost, getBlogImage } from "@/utils/blog";
import DisqusThread from "@/components/DisqusThread";

// Import styles for markdown if needed, e.g. github-markdown-css
// import 'github-markdown-css'; 

export default {
  name: "BlogPost",
  components: {
    DisqusThread,
    ShareButtons: () => import("@/components/ShareButtons"),
  },
  data() {
    return {
      post: null,
    };
  },
  computed: {
    language() {
      return this.$store.getters.language;
    },
    processedHtml() {
      if (!this.post || !this.post.html) return "";

      // Replace relative image paths with resolved require paths
      const container = document.createElement("div");
      container.innerHTML = this.post.html;

      const images = container.querySelectorAll("img");
      images.forEach((img) => {
        const src = img.getAttribute("src");
        if (src && (src.startsWith("./") || !src.startsWith("http"))) {
          const resolved = getBlogImage(this.post.slug, src);
          if (resolved) {
            img.setAttribute("src", resolved);
          }
        }
      });
      
      return container.innerHTML;
    }
  },
  created() {
    this.loadPost();
  },
  watch: {
    "$route.params.slug": "loadPost",
    language: "loadPost",
  },
  methods: {
    loadPost() {
      const slug = this.$route.params.slug;
      this.post = getBlogPost(slug, this.language);
    }
  }
};
</script>



<style>
