.markdown-body img {
  max-width: 100%;
  border-radius: 8px;
  margin: 1rem 0;
}
.markdown-body h1, .markdown-body h2, .markdown-body h3 {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}
.markdown-body p {
  margin-bottom: 1rem;
  line-height: 1.6;
}
</style>
