<template>
  <div style="width: 100%; max-width: 1400px; margin: 0 auto; padding: 0 1rem;">
    <!-- Controls Section -->
    <v-row class="mb-6 align-center ma-0" justify="center" style="gap: 1rem;">
      <v-col cols="12" md="6" lg="5">
        <v-text-field
          v-model="search"
          label="Search Fighters"
          prepend-inner-icon="mdi-magnify"
          outlined
          dense
          hide-details
          background-color="white"
          class="search-field"
        ></v-text-field>
      </v-col>
      <v-col cols="auto">
        <v-chip-group
          v-model="filters"
          multiple
          active-class="primary--text"
        >
          <v-chip filter value="patreon" outlined class="filter-chip">
            <v-img src="@/assets/patreon_filter.svg" width="16" height="16" contain class="mr-2"/>
            Patreon
          </v-chip>
          <v-chip filter value="editable" outlined class="filter-chip">
            <v-icon left size="18">mdi-pencil</v-icon>
            Editable
          </v-chip>
        </v-chip-group>
      </v-col>
    </v-row>

    <!-- Gallery Grid -->
    <v-row class="ma-0" style="gap: 2rem; justify-content: center;">
      <v-col
        v-for="fighterDesign in filteredDesigns"
      :key="fighterDesign.fileName"
      class="d-flex child-flex pa-0"
      cols="12"
      sm="4"
      lg="3"
      xl="2"
    >
      <div 
        class="gallery-item"
        style="position: relative; width: 100%; aspect-ratio: 1 / 1; overflow: hidden; border-radius: 4px; box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);"
        @click="onClick(fighterDesign.fileName)"
      >
        <v-img
          :src="galleryPath(fighterDesign.image)"
          aspect-ratio="1"
          class="grey lighten-2 gallery-image"
          style="object-fit: cover; width: 100%; height: 100%; transition: transform 0.7s;"
        />
        
        <!-- Caption Overlay with Glassmorphism -->
        <div class="gallery-caption glass-effect">
          {{ fighterDesign.name }}
        </div>

        <!-- Badges -->
        <div class="badge-container">
          <img
            v-if="fighterDesign.patreon"
            src="@/assets/patreon.svg"
            alt="Patreon"
            class="badge-icon static-badge"
          />
          <template v-else>
            <div class="badge-icon editable-badge static-badge">
              <v-icon color="white" size="20">mdi-pencil</v-icon>
            </div>
            <img
              v-if="fighterDesign.gumroadPermalink"
              src="@/assets/gumroad.svg"
              alt="Gumroad"
              class="badge-icon static-badge gumroad-badge"
            />
          </template>
        </div>
      </div>
    </v-col>
    </v-row>
  </div>
</template>

<style scoped>
