  transform: scale(1.1) rotate(5deg);
}

.search-field ::v-deep .v-input__slot {
  min-height: 48px !important;
}

.filter-chip {
  background-color: white !important;
  border-color: rgba(0,0,0,0.1) !important;
}

.filter-chip.v-chip--active {
  border-color: #1a1a40 !important;
  background-color: #f0f0f5 !important;
  color: #1a1a40 !important;
}
</style>

<script>
import { fighterDesigns } from "@/data/fighterDesigns.js";

export default {
  data() {
    return {
      fighterDesigns,
      search: "",
      filters: ["patreon", "editable"],
    };
  },
  computed: {
    filteredDesigns() {
      let designs = this.fighterDesigns;

      // Filter by Search
      if (this.search) {
        const normalize = (str) => str.toLowerCase().replace(/[^a-z0-9]/g, "");
        const query = normalize(this.search);
        
        designs = designs.filter((design) => {
          // Match against name
          if (normalize(design.name).includes(query)) return true;
          // Match against filename as fallback (often contains useful keywords)
          if (normalize(design.fileName).includes(query)) return true;
          return false;
        });
      }

      // Filter by Type (Patreon / Editable)
      designs = designs.filter((design) => {
        const isPatreon = !!design.patreon;
        const isEditable = !design.patreon; // Assuming editable if not patreon, based on badge logic

        let match = false;
        if (this.filters.includes("patreon") && isPatreon) match = true;
        if (this.filters.includes("editable") && isEditable) match = true;
        return match;
      });

      return designs;
    },
  },
  methods: {
    galleryPath(image) {
      return process.env.BASE_URL + "gallery/" + image;
    },
    onClick(fighter) {
      this.$router.push(`/fighters/${fighter}`);
    },
  },
};
</script>
