function findFighterDesign(fighter) {
  if (!fighter) return null;
  return fighterDesigns.find((each) => each.fileName === fighter);
}

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min)) + min;
}

function getRandomFighterDesign() {
  let i = getRandomInt(0, fighterDesigns.length);
  return fighterDesigns[i];
}

function getLastFighterDesign() {
  return fighterDesigns[fighterDesigns.length - 1];
}

export {
  fighterDesigns,
  findFighterDesign,
  getRandomFighterDesign,
  getLastFighterDesign,
};
