<script>
"use strict";

import Editor from "@/components/Editor";
import Gallery from "@/components/Gallery";
import Privacy from "@/components/Privacy";
import GumroadLicenseModal from "@/components/GumroadLicenseModal";
// import Disqus from "@/components/Disqus";
import { findFighterDesign } from "@/data/fighterDesigns.js";
import * as firebase from "@/firebase.js";

export default {
  name: "Home",

  components: {
    Editor,
    Gallery,
    Privacy,
    GumroadLicenseModal,
    // Disqus,
  },

  data() {
    return {
      analytics: firebase.analytics,
      editorDialog: false,
      privacyDialog: false,
      fighter: null,
      authInfo: null,
      overlay: false,
      userId: null,
      // logoSvg: '/assets/logo/logo.svg' // Moved to App.vue
      gumroadModal: false,
      currentPermalink: "",
      currentProductId: "",
      currentProductName: "",
      pendingFighter: null,
    };
  },
  mounted() {
    firebase.addLoginCallback(this.onChangefirebaseInfo);
    this.onChangeRoute(this.$route);
  },
  methods: {
    onChangefirebaseInfo(authInfo) {
      this.authInfo = authInfo;
      if (authInfo == null) {
        console.log("onChangefirebaseInfo(null)");
      } else {
        console.log(
          `onChangefirebaseInfo(${JSON.stringify(authInfo.user.uid)})`,
        );
        firebase.setUser(authInfo.user.uid);
      }
      this.overlay = false;
    },
    isLoggedIn() {
      return this.authInfo != null;
    },

    // logoPath() Removed as we use static SVG now
    openEditorDialog(fighter) {
      this.fighter = fighter;
      this.editorDialog = true;
    },
    closeEditorDialog() {
      this.fighter = null;
      this.editorDialog = false;
    },
    openPrivacyDialog() {
      this.privacyDialog = true;
    },
    closePrivacyDialog() {
      this.privacyDialog = false;
    },
    getFighter(route) {
      if (route === undefined) {
        return null;
      } else {
        const fighter = route.params.fighter;
        const design = findFighterDesign(fighter);
        if (design == null) {
          return null;
        }
        return fighter;
      }
    },
    getPatreonUrl(fighter) {
      const design = findFighterDesign(fighter);
      if (design == null) {
        return null;
      }
      return design.patreon;
    },
    onChangeRoute(route) {
      if (route.path === "/privacy") {
        this.openPrivacyDialog();
      } else if (route.path.startsWith("/fighters/")) {
        const fighter = this.getFighter(route);
        const patreonUrl = this.getPatreonUrl(fighter);
        if (patreonUrl) {
          window.open(patreonUrl, "_blank");
          // window.history.back();
        } else if (fighter == null) {
          this.closeEditorDialog();
        } else {
          this.attemptOpenEditor(fighter);
        }
      } else {
        this.closeEditorDialog();
        this.closePrivacyDialog();
      }
    },
    attemptOpenEditor(fighter) {
      const design = findFighterDesign(fighter);
      if (design && design.gumroadPermalink) {
        const license = localStorage.getItem(
          `gumroad_license_${design.gumroadPermalink}`
        );
        if (!license) {
          this.pendingFighter = fighter;
          this.currentPermalink = design.gumroadPermalink;
          this.currentProductId = design.gumroadProductId || "";
          this.currentProductName = design.name;
          this.gumroadModal = true;
          return;
        }
      }
      this.openEditorDialog(fighter);
    },
    onLicenseVerified({ permalink }) {
      localStorage.setItem(`gumroad_license_${permalink}`, "true");
      if (this.pendingFighter) {
        this.openEditorDialog(this.pendingFighter);
        this.pendingFighter = null;
      }
    },
    closeGumroadModal() {
      this.gumroadModal = false;
      this.pendingFighter = null;
      this.$router.replace('/');
    },
  },
  watch: {
    $route(route) {
      this.onChangeRoute(route);
    },
  },
};
</script>
