<template>
  <v-app>
    <header>
      <div class="header-inner">
        <router-link to="/" class="logo-section">
          <img :src="logoSvg" alt="Origami Skin Logo" />
          <span class="brand-name">ORIGAMI SKIN</span>
        </router-link>
        <nav>
          <router-link to="/blog" class="nav-link">Blog</router-link>
          
          <button 
            @click="toggleLanguage" 
            class="lang-btn"
          >
            {{ currentLang === 'ko' ? 'EN' : 'KO' }}
          </button>
        </nav>
      </div>
    </header>

    <v-main class="main-content">
      <router-view />
      <Footer />
    </v-main>
  </v-app>
</template>

<script>
import Footer from "@/components/Footer.vue";

export default {
  name: "App",
  components: {
    Footer,
  },
  data() {
    return {
      logoSvg: '/assets/logo/logo.svg'
    };
  },
  computed: {
    currentLang() {
      return this.$store.getters.language;
    }
  },
  created() {
    // Only init if not already persisted (check if store has non-default value logic if needed)
    // But simplest is: if localStorage is empty, run init.
    // However, vuex-persistedstate runs automatically.
    // If we want to respect browser default on FIRST visit, we should check if 'language' is in localStorage.
    
    // For now, let's keep it simple: 
    // If state is 'en' (default), we might want to check browser. 
    // BUT 'en' is a valid choice.
    // A better way is checking if persisted state exists.
    // Let's rely on manual toggle mainly, and simple init logic.
    
    if (!localStorage.getItem("vuex")) {
      this.$store.dispatch("initLanguage");
    }
  },
  methods: {
    toggleLanguage() {
      const newLang = this.currentLang === 'ko' ? 'en' : 'ko';
      this.$store.dispatch("setLanguage", newLang);
    }
  }
};
</script>

